import React, { useState, useEffect } from "react";
import "../App.css";

const Product = () => {
   const btn = {
      background: "#fff",
      color: "#444",
      padding: "6px 8px",
      borderRadius: "8px",
      display: "inline-block",
      border: "1px solid #ccc",
      cursor: "pointer",
   };
   const imageStyle = {
      width: "350px",
      height: "300px",
      objectFit: "cover",
      objectPosition: "center",
   };

   // !API project

   useEffect(() => {
      fetchItems();
   }, []);

   const [items, setItems] = useState([]);

   const fetchItems = async () => {
      const data = await fetch(	
         "https://api.unsplash.com/photos/random?client_id=kBZx2ozRmznn4hyAiOBBRgZHkPknkZ7Ew95W5j1AG6A&count=30"
      );

      const items = await data.json();
      setItems(items);
		console.log(items);
		
		
		
      const cardDescription = () => {
         let cardText = document.querySelectorAll(".cardWrap p");
         for (let i = 0; i < cardText.length; i++) {
            if (cardText[i].textContent === "") {
               cardText[i].textContent = "Khamidullaev Abbos";
            }
			}
			
      };
      cardDescription();

      const like = () => {
         let likeBtn = document.querySelectorAll(".fa-heart");

         for (let i = 0; i < likeBtn.length; i++) {
            likeBtn[i].addEventListener("click", function () {
               likeBtn[i].classList.toggle("heartLike");
            });
         }
      };
      like();
   };
   // !API project

   return (
      <div>
         <h1>...</h1>

         <div className="wrapper">
            {items.map((item) => (
               <div className="cardWrap">
                  <p className="desctiption">{item.alt_description}</p>
                  {/* content images */}
                  <img
                     id="contentImg"
                     loading="lazy"
                     src={item.urls.small}
                     alt="image"
                     style={imageStyle}
                  />

                  <div className="card__text">
                     {/* Profil images */}
                     <img
                        loading="lazy"
                        src={item.user.profile_image.medium}
                        alt="images"
                     />
                     <a
                        href={
                           "https://www.instagram.com/" +
                           item.user.instagram_username
                        }
                        target="_blank"
                     >
                        <p>{item.user.name}</p>
                     </a>

                     <div className="cardBtns">
                        <i className="fas fa-heart" style={btn}></i>
                        <a
                           rel="nofollow"
                           href={item.urls.small}
                           download
                           target="_blank"
                           className="downBtn"
                           style={btn}
                        >
                           <i className="fas fa-arrow-down"></i>
                        </a>
                     </div>
                  </div>
               </div>
            ))}
         </div>
      </div>
   );
};

export default Product;
