import React from "react";
import "./Navbar.css";
import { Link } from "react-router-dom";

const Navbar = () => {
   return (
      <div>
         <div className="nav">
            <div className="nav__logo">Logo</div>
            <ul className="nav__list">
               <Link to="/">
                  <li className="navlink">
                     <a href="#">Home</a>
                  </li>
               </Link>
               <Link to="/Product">
                  <li className="navlink">
                     <a href="#">Products</a>
                  </li>
               </Link>
               <Link to="/About">
                  <li className="navlink">
                     <a href="#">About</a>
                  </li>
               </Link>
            </ul>
         </div>
      </div>
   );
};

export default Navbar;
